import React from "react";
import {
  Grid,
  Box,
  Typography,
  Card,
  CardContent,
  CardMedia,
} from "@mui/material";

// Define a type for the coupon item
type Coupon = {
  title: string;
  store: string;
  code: string;
  icon: string;
};

const Hero: React.FC = () => {
  // Coupon data with proper typing
  const coupons: Coupon[] = [
    {
      title: "Free delivery",
      store: "TECH SHOP",
      code: "12oOTjcF3z",
      icon: "fas fa-truck",
    },
    {
      title: "10% Off",
      store: "6Valley",
      code: "rtzxxBnBlr",
      icon: "fas fa-dollar-sign",
    },
    {
      title: "75% Off",
      store: "TECH SHOP",
      code: "rfhfx7xlCm",
      icon: "fas fa-dollar-sign",
    },
  ];

  // Categories data
  const categories: string[] = [
    "Network Components",
    "Laptop, Tabs & Notebooks",
    "Consumer electronics",
    "Gadgets",
    "Computer & Office",
    "Mobile Accessories",
    "Smartphone",
    "Wearable",
    "Camera Accessories",
    "TV & Home Appliance",
    "Audio",
  ];

  return (
    <Box className="flex p-4">
      {/* Left side navigation */}
      <Box className="w-1/4">
        <ul className="space-y-2">
          {categories.map((item, index) => (
            <li key={index} className="flex justify-between items-center">
              <span>{item}</span>
              <i className="fas fa-chevron-right"></i>
            </li>
          ))}
        </ul>
        <div className="mt-4 text-blue-600">
          <a href="#">View all</a>
        </div>
      </Box>

      {/* Right side content */}
      <Box className="w-3/4 flex flex-col space-y-4">
        <Grid container spacing={2}>
          <Grid item xs={8}>
            {/* Main image section */}
            <Card>
              <CardMedia
                component="img"
                height="300"
                image="https://placehold.co/600x300"
                alt="Special Offer on mobile phones"
                className="rounded-lg"
              />
            </Card>
          </Grid>

          <Grid item xs={4}>
            {/* Coupons section */}
            <Card className="p-4 rounded-lg shadow">
              <CardContent>
                <Typography variant="h6" className="font-bold mb-4">
                  Happy Club
                </Typography>
                <Typography variant="body2" className="mb-4">
                  Collect coupons from stores and apply to get special discounts
                </Typography>
                <Box className="space-y-4">
                  {coupons.map((coupon, index) => (
                    <Box key={index} className="border p-2 rounded-lg">
                      <Typography className="font-bold">
                        {coupon.title} <i className={coupon.icon}></i>
                      </Typography>
                      <Typography>For {coupon.store}</Typography>
                      <Typography className="text-blue-600">
                        Code: {coupon.code}
                      </Typography>
                    </Box>
                  ))}
                </Box>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        {/* Additional Images */}
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <Card>
              <CardMedia
                component="img"
                height="150"
                image="https://placehold.co/300x150"
                alt="Black Friday limited time offer"
                className="rounded-lg"
              />
            </Card>
          </Grid>
          <Grid item xs={6}>
            <Card>
              <CardMedia
                component="img"
                height="150"
                image="httpss://placehold.co/300x150"
                alt="Black Friday sale on all items"
                className="rounded-lg"
              />
            </Card>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default Hero;
