import React, { useState } from "react";
import {
  Box,
  Typography,
  IconButton,
  Badge,
  Divider,
  Menu,
  MenuItem,
  Container,
} from "@mui/material";
import {
  SyncAlt,
  ExpandMore,
  Favorite,
  ShoppingBag,
  Star,
} from "@mui/icons-material";
import MenuIcon from "@mui/icons-material/Menu";

const NavBar: React.FC = () => {
  // State for each dropdown
  const [categoryAnchorEl, setCategoryAnchorEl] =  useState<null | HTMLElement>(
    null
  );
  const [offersAnchorEl, setOffersAnchorrEl] = useState<null | HTMLElement>(
    null
  );
  const [storesAnchorEl, setStoresAnachorEl] = useState<null | HTMLElement>(
    null
  );
  const [brandsAnchorEl, setBrandsAnchorEl] = useState<null | HTMLElement>(
    null
  );

  // Handlers for dropdown toggles
  const handleCategoryClick = (event: React.MouseEvent<HTMLElement>) => {
    setCategoryAnchorEl(event.currentTarget);
  };

  const handleOffersClick = (event: React.MouseEvent<HTMLElement>) => {
    setOffersAnchorrEl(event.currentTarget);
  };

  const handleStoresClick = (event: React.MouseEvent<HTMLElement>) => {
    setStoresAnachorEl(event.currentTarget);
  };

  const handleBrandsClick = (event: React.MouseEvent<HTMLElement>) => {
    setBrandsAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setCategoryAnchorEl(null);
    setOffersAnchorrEl(null);
    setStoresAnachorEl(null);
    setBrandsAnchorEl(null);
  };

  return (
    <Box className="bg-white shadow-md">
      <Container maxWidth={"xl"}>
        <Box className="flex flex-wrap items-center justify-between bg-white p-2">
          {/* Left Section - Category Dropdown */}
          <Box className="flex items-center bg-blue-700 p-2 w-full md:w-auto">
            <IconButton onClick={handleCategoryClick}>
              <MenuIcon className="text-white" />
            </IconButton>
            <Typography onClick={handleCategoryClick} className="text-white ml-2 cursor-pointer">Select Category</Typography>
            <IconButton onClick={handleCategoryClick}>
              <ExpandMore className="text-white" />
            </IconButton>

            {/* Select Category Dropdown */}
            <Menu anchorEl={categoryAnchorEl} open={Boolean(categoryAnchorEl)} onClose={handleClose}>
              <MenuItem onClick={handleClose}>Category 1</MenuItem>
              <MenuItem onClick={handleClose}>Category 2</MenuItem>
              <MenuItem onClick={handleClose}>Category 3</MenuItem>
            </Menu>
          </Box>

          {/* Middle Section - Navigation Links */}
          <Box className="flex flex-wrap items-center space-x-4 mt-2 md:mt-0 w-full md:w-auto">
            <Typography component="a" href="#" className="text-gray-700">
              HOME
            </Typography>

            {/* Offers Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleOffersClick}
            >
              OFFERS
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu anchorEl={offersAnchorEl} open={Boolean(offersAnchorEl)} onClose={handleClose}>
                <MenuItem onClick={handleClose}>Offer 1</MenuItem>
                <MenuItem onClick={handleClose}>Offer 2</MenuItem>
                <MenuItem onClick={handleClose}>Offer 3</MenuItem>
            </Menu>

            {/* Stores Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleStoresClick}
            >
              STORES
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu anchorEl={storesAnchorEl} open={Boolean(storesAnchorEl)} onClose={handleClose}>
                <MenuItem onClick={handleClose}>Store 1</MenuItem>
                <MenuItem onClick={handleClose}>Store 2</MenuItem>
                <MenuItem onClick={handleClose}>Store 3</MenuItem>
            </Menu>

            {/* Brands Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleBrandsClick}
            >
              BRANDS
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu anchorEl={brandsAnchorEl} open={Boolean(brandsAnchorEl)} onClose={handleClose}>
                <MenuItem onClick={handleClose}>Brand 1</MenuItem>
                <MenuItem onClick={handleClose}>Brand 2</MenuItem>
                <MenuItem onClick={handleClose}>Brand 3</MenuItem>
            </Menu>

            {/* Discounted Products */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
            >
              DISCOUNTED PRODUCTS
              <Star className="text-yellow-500 ml-1" fontSize="small" />
            </Typography>
          </Box>

          {/* Right Section - Icons and User Actions */}
          <Box className="flex items-center space-x-4 mt-2 md:mt-0 w-full md:w-auto justify-center">
            <Typography component="a" href="#" className="bg-blue-600 px-4 py-2 text-white rounded">
              Login / Register
            </Typography>
            <Divider
              orientation="vertical"
              flexItem
              className="border-gray-300"
            />
            <IconButton className="text-gray-700">
              <SyncAlt />
            </IconButton>
            <IconButton className="text-gray-700">
              <Favorite />
            </IconButton>
            <Divider
              orientation="vertical"
              flexItem
              className="border-gray-300"
            />
            <Box className="relative">
              <IconButton className="text-gray-700">
                <Badge badgeContent={0} color="primary">
                  <ShoppingBag />
                </Badge>
              </IconButton>
            </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default NavBar;
